import React, { useEffect, useRef } from 'react';

export default function CustomCountUp({ numberCounter }) {
  const countupRef = useRef(null);
  let countUpAnim = useRef(null); // S dng useRef  gi tham chiu n animation

  useEffect(() => {
    // nh ngha hm async bn trong useEffect
    async function initCountUp() {
      const countUpModule = await import('countup.js');
      countUpAnim.current = new countUpModule.CountUp(
        countupRef.current,
        numberCounter,
        {
          enableScrollSpy: true,
          duration: 3,
          scrollSpyOnce: true,
          formattingFn: (value) => {
            // Chuyn gi tr s thnh chui v loi b du phn cch
            return value.toFixed(0);
          },
        },
      );
      if (!countUpAnim.current.error) {
        countUpAnim.current.start();
      } else {
        console.error(countUpAnim.current.error);
      }
    }
    initCountUp();

    // Hm dn dp
    return () => {
      if (countUpAnim.current) {
        countUpAnim.current.pauseResume(); // Hoc phng thc ph hp  dng animation
      }
    };
  }, [numberCounter]); // Thm numberCounter vo dependency array

  return <span ref={countupRef}>0</span>;
}
